.dashboard-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .dashboard-title {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .tabs-list {
    display: flex;
    gap: 12px;
  }
  
  .tab-trigger {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .dropdown-container {
    margin-bottom: 1rem;
  }
  
  .select-trigger {
    width: 100%;
    max-width: 200px;
    padding: 8px 12px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .chart-container {
    display: flex;
    align-items: flex-start;
    gap: 12px;
    width: 100%;
  }
  
  .labels-container {
    width: 120px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .label-item {
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    white-space: nowrap;
  }
  
  .bar-chart-wrapper {
    flex: 1;
    min-width: 0;
  }
  